body {
	font-family: 'Karla', Arial, sans-serif;
	@include f(16, 24, null, 400);
	color: $white;
	@include font-smoothing;
	background: $black;
	position: fixed;
	overflow-y: hidden;
	&.unfix {
		position: static;
		overflow-y: auto; } }

.out {
	position: relative;
	min-height: 100%;
	overflow: hidden;
	transition: transform .6s ease-in;
	[data-body-animate] & {
		transform: translateY(100vh); }
	.hide-help & {
		transform: translateY(0); } }
.clouds {
	opacity: 0;
	transition: opacity 1s ease-in-out;
	.unfix & {
		opacity: 1; }
	[data-body-animate] & {
		opacity: 0; }
	.hide-help & {
		transition-delay: 0.9s;
		opacity: 1; } }

[data-body-animate].unfix {
	overflow: hidden; }

.inner {
	position: relative;
	padding: 0 15px;
	margin: 0 auto;
	z-index: 1;
	max-width: 1200px;
	@include clr;
	@include md {
		padding: 0 25px; }
	@include sm {
		padding: 0 15px; } }

.error-block {
	display: none; }

.md-show {
	display: none !important;
	@include sm {
		display: block !important; } }
